import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { AddProductComponent } from '../add-product/add-product.component';
import { IProduct } from '../data/iproduct';
import { Timespan } from '../data/timespan';
import { ProductsService } from '../products.service';

@Component({
  selector: 'app-overview',
  templateUrl: './overview.page.html',
  styleUrls: ['./overview.page.scss'],
})
export class OverviewPage implements OnInit {
  public readonly Timespan = Timespan;
  public viewTimespan = 0;
  public Object = Object;
  modal: HTMLIonModalElement = null;

  asyncProductRange: Promise<Array<any>>; // Promise<Array<IProduct[]>>;
  productSubscription: Subscription;
  constructor(
    public productsService: ProductsService,
    public modalController: ModalController,
    public alertController: AlertController) {
    this.updateProducts();
    this.productSubscription = productsService.productListChange().subscribe(
        this.updateProducts.bind(this)
      // this.productsService.getProductsByTimespan.bind(this, this.viewTimespan)
    );
  }
  async generateModal(product?: IProduct) {
    this.modal = await this.modalController.create({
      component: AddProductComponent,
      componentProps: {
        product
      }
    });
    await this.modal.present();
    await this.modal.onWillDismiss();
  }
  async deleteProduct(product: IProduct) {
    const alert = await this.alertController.create({
      header: 'Dieses Produkt wirklich löschen?',
      buttons: [
        {
          text: 'Nein',
          role: 'cancel',
          cssClass: 'secondary',
        }, {
          text: 'Ja',
          handler: () => {
            this.productsService.deleteProduct(product);
          }
        }
      ]
    });
    alert.present();
  }
  async editProduct(product: IProduct) {
    this.generateModal(product);
  }
  updateProducts() {
    this.asyncProductRange = this.productsService.getProductsByTimespan(this.viewTimespan);
  }
  changeTimespan(event) {
    this.viewTimespan = +event.target.value;
    this.updateProducts();
  }

  ngOnInit() {
  }
}
